import React, { Component } from 'react'
import { Nav, Navbar } from 'react-bootstrap'
import { Link, withRouter } from 'react-router-dom'

class Header extends Component {

    render() {
        return (
            <Navbar collapseOnSelect expand="lg" fixed="top" style={{ backgroundColor: "#202020" }}>

                <Nav >
                    <Navbar.Brand>
                        <Nav.Link as={Link} to="/Home"><span>Plant</span>Talk</Nav.Link>
                    </Navbar.Brand>
                </Nav>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="ml-auto mr-4">
                        <Nav.Link eventKey={1} as={Link} to="/Home">Home</Nav.Link>
                        <Nav.Link eventKey={2} as={Link} to="/Plants">Plants</Nav.Link>
                        <Nav.Link eventKey={3} as={Link} to="/FindPlants">Find Plants</Nav.Link>
                        <Nav.Link eventKey={5} as={Link} to='/Suggestions'>Suggestions</Nav.Link>
                        <Nav.Link eventKey={6} as={Link} to='/Feedback' > Feedback</Nav.Link>
                        <Nav.Link eventKey={6} as={Link} to='/OurTeam' > Our Team</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        )
    }
}

export default withRouter(Header);